.test-border {
    border: 1px solid red;
}

html, body {
    height: 100%;
    width: 100%;
}

#root {
    height: 100%;
    width: 100%;
    background-color: rgb(96, 109, 255);
}

.message-gallery {
    background-color: white;
    width: 60%;
    height: 80%;
    border: outset;
}

.dialogue-participant {
    height: 10%;
}

.message-wall {
    height: 80%;
}

.message-prompt {
    height: 10%;
}

.input-prompt {
    border: 1px solid #198754;
    outline: none;
}

.border-curve-10 {
    border-radius: 10px;
}


@media only screen and (max-width: 600px) {
    .message-gallery {
        background-color: white;
        width: 98%;
        height: 80%;
        border: outset;
    }
}